body{
  top: 0;
  margin: 0;
  left: 0;
  background-color: rgba(12, 12, 25, 1);
}
.container {
  width: 100%;
}
.container::after {
  content: "";
  display: table;
  clear: both;
}
.head-list {
  padding: 50px;
  display: block;
  display: flex;
  align-items: center;
  flex-direction: column;
  line-height: 0.3;
}
.price-details {
  display: flex;
  justify-content: center;
}
.box {
  display: flex;
  align-items: baseline;
  background-color: rgba(19, 20, 25, 1);
  width: 28%;
  height: 900px;
  border-radius: 5%;
  flex-direction: column;
  margin: 12px;
}
img {
  padding: 6px 0 0 6px;
  background-color: rgb(28, 143, 44);
  background: none;
  height: 30px;
  width: 30px;
}
.logo {
  display: flex;
  align-items: first baseline;
}

.circle {
