}
ul {
  list-style: none;
}
p {
  color: rgb(148,151,158);
  font-family: inter,system-ui,Arial, Helvetica, sans-serif;
  font-weight: lighter;
}
.faq-main {
  display: flex;
  justify-content: flex-start;
}

.faq-container {
  max-width: 800px;
  margin: 10px auto;
  padding: 10px;
  border-radius: 8px;
  border-bottom: 1px solid #292626;
  
}

.faq-question {
  margin-bottom: 10px;
  cursor: pointer;
  color: antiquewhite;
}
.faq-question :hover {
  color: azure;
}

.faq-answer {
  display: none;
  margin-bottom: 20px;
}

.faq-answer {
  background-color: rgba(250, 235, 215, 0);
}

.frame_kit {
  width: 100%;
  height: 100vh;
  overflow-y: scroll;
}

.frame_kit1 {
  max-width: 800px;
  margin: 50px auto;
}

h2 {
  margin-bottom: 20px;
  font-weight: normal;
  font-family: sans-serif;
}

h2.active {
  font-weight: bold;
  color: white;

}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

ul li {
  margin-bottom: 10px;
  color: white;
  border-bottom: 1px solid #292626;
  padding: 10px;

}
li {
  font-family: sans-serif;
  font-size: smaller;
}

